const keys = () => [1, 2, 3, 4, 5, 6, 7, 8, 9, '', 0]
// let sendFlag = true // 防止重复发送密码
export default {
  data () {
    return {
      items: [0, 1, 2, 3, 4, 5],
      keys: keys(),
      password: [],  
    }
  },
  props:{
    valShow : {
      type : Boolean,
      default : false
    },
  },
  watch:{
      valShow(val){
          console.log(val)
      }
  },
  methods: {
    backHandle () {
      this.clearPasswordHandle()  // 返回时清除password
      this.$emit('backHied') // 返回上级
      
    },
    keyUpHandle (e) {
      
      let text = e.currentTarget.dataset.number.toString()
      console.log(e)
      let len = this.password.length
      if ( !text || len >= 6) return
      this.password.push(text)
      this.ajaxData()
    },
    delHandle () {
      if (this.password.length <= 0) return false
      this.password.shift()
    },
    ajaxData () {
      if (this.password.length >= 6) {
          let data = {
              cord:parseInt(this.password.join(' ').replace(/\s/g, '')),
              backHied:false
          }
          this.clearPasswordHandle()
          this.$emit('cord_info',data)
          
      }
      return false
    },
    clearPasswordHandle: function () {
      this.password = []
    },
   
  }
}
